body{
   background-color:#ccddff; 
}
.app{
    text-align:center;
}
.container{
    background-color:rgb(7,2,71);
    justify-content:center;
    width:50%;
    margin-left:26%;
    margin-top:5%;
    text-align:center;
    padding:20px;
}
input[type=text],select{
    width:80%;
    padding:12px 20px;
    margin:30px 0px;
    display:inline-block;
    border:1px solid #ccc;
    border-radius:4px;
    box-sizing:border-box;
}
#add{
    width:12%;
    background-color:#4CAF50;
    color:black;
    padding:14px 20px;
    margin:8px;
    border:none;
    border-radius:4px;
    cursor:pointer;
    font-weight:bold;
    position:relative;

}
#add:hover{
    background-color:#45a049;
}
.header{
    width:100%;
    padding:12px 20px;
    color:white;
    font-size:50px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.List-heading{
    color:rgb(176, 37, 37);
    font-size:50px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align:left;
    background-color:rgb(137, 154, 155);
    width:60%;
}
.listData{
    color:black;
    font-size:20px;
    background-color:rgb(164,203,237);
    text-align:left;
    padding-left:10px;
    width:53%;
    padding-bottom:2%;
    padding-top:2%;
    border-radius:10px;
}
.btn-position{
    position:relative;
    left:11%;
    top:-78px;
    
}